import React, { useEffect } from 'react';

import { fonts } from '../constants';

export default function BlockHubspotForm() {
  useEffect(() => {
    const script = document.createElement('script');
    script.src = '//js.hsforms.net/forms/v2.js';
    document.body.appendChild(script);
    script.addEventListener('load', () => {
      if (window.hbspt) {
        window.hbspt.forms.create({
          portalId: '8982602',
          formId: '00761d29-60f5-4b4c-afd9-0882c320d2f5',
          target: '#hubspotForm',
        });
      }
    });
  }, []);

  return (
    <div>
      <p className="contact_sales__subtitle">
        Tell us how to reach you and let's discuss how Resolve can grow your b2b
        business.
      </p>
      <div className="contact_sales_form__form-container">
        <div id="hubspotForm"></div>
      </div>
      <style jsx>{`
        .clearfix::after {
          content: '';
          clear: both;
          display: table;
        }
        .contact_sales__subtitle {
          font-family: ${fonts.halyardDisplay};
          font-size: 22px;
          line-height: 1.64;
          letter-spacing: 0.8px;
          margin: 20px 0 40px;
          text-align: center;
        }
        .resolve-row {
          margin-left: -15px;
          margin-right: -15px;
        }
        .resolve-col {
          float: left;
          width: 50%;
          padding: 0 15px;
        }
        @media (max-width: 767px) {
          .resolve-col {
            float: none;
            width: 100%;
          }
        }
      `}</style>
      <style jsx global>{`
        .contact_sales_form__submit {
          width: 100%;
        }
        .contact_sales__container * {
          box-sizing: border-box;
        }
      `}</style>
    </div>
  );
}
